}
.navbar > .container > .navbar-collapse {
	background-color: #FFF;
	margin: -2rem;
	margin-top: 7vh;
	padding: 2rem;
	overflow: visible;
	border-bottom: 1px solid #eee;
}
.navbar-collapse {
	max-height: 100%;
}
