.notepad {
	position: relative;
	margin: 0 2rem 2rem 0;
	background: #FFFF8D;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
	overflow: hidden;
}
.notepad:before {
	content: ' ';
	position: absolute;
	top: 0;
	right: auto;
	bottom: 0;
	left: 0;
	width: 50px;
	background: radial-gradient(#575450 6px, transparent 7px);
	background-size: 30px 30px;
	background-repeat: repeat-y;
	border-right: 3px solid #D44147;
}

.notepad-ingredients {
	width: calc(100% - 49px);
	position: absolute;
	top: 30px;
	right: 0px;
	bottom: 30px;
	left: 50px;
	background: linear-gradient(transparent, transparent 28px, #91D1D3 28px);
	background-size: 30px 30px;
}
.notepad-ingredients table {
  margin: 0px;
  padding: 0px;
}
